body, html {
  margin: 0;
  padding: 0;
  background-color: black;
}

.calculator {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  background-color: black;
  padding: 8px;
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.display {
  background-color: black;
  color: white;
  font-size: 4.5em;
  padding: 20px;
  text-align: right;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 8px;
}

button {
  font-size: 2em;
  border: none;
  background-color: #505050;
  color: white;
  cursor: pointer;
  border-radius: 50%;
  aspect-ratio: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.calculator-icon {
  width: 24px;
  height: 24px;
}

button.gray {
  background-color: #a5a5a5;
}

button.orange {
  background-color: #ff9500;
}